.SA-Map {
  position: relative;
  width: 40%;
  height: 100%;
  background-color: #20254b;
  border-radius: 10px;
  overflow: hidden;
}

.SA-Map figure {
  /* margin: -5em; */
  display: flex;
  width: 100%;
  /* padding: 1em; */
  opacity: 0.25;
}

.SA-Map figure img {
  width: 100%;
  margin: auto;
}

.SA-Map > h4 {
  font-size: 1.25rem;
  position: absolute;
  top: 1.25em;
  left: 1.5em;
}

.SA-Map footer {
  position: absolute;
  bottom: 2em;
  left: 2em;
}

.SA-Map footer h4 {
  color: #007a4d;
}

.SA-Map footer h3 {
  margin: 0.3em 0 0.8em;
}

.SA-Map footer button {
  color: #fff;
  padding: 0.5em 0.8em;
  border: 1px solid #ffb612;
  background-color: #ffb612;
  border-radius: 35px;
}

.map {
  position: absolute;
  top: 3em;
  width: 82%;
  height: 90%;
  background-color: #fff;
  border-radius: 10px;
  background-color: #20254b;
}
