.National-LineChart {
  /* display: flex; */

  width: 58.5%;
  height: 100%;
  background-color: #20254b;
  border-radius: 10px;
}

.National-LineChart .linechart {
  width: 100%;
  /* margin: 0.8em auto 0; */
  /* padding-left: 1em; */
}

/* AXES */
/* ticks */
.National-LineChart .axis line {
  /* stroke: #706f6f; */
  stroke-width: 0.5;
  shape-rendering: crispEdges;
}

/* axis contour */
.National-LineChart .axis .National-LineChart path {
  stroke: #706f6f;
  stroke-width: 0.7;
  shape-rendering: crispEdges;
}

/* axis text */
.National-LineChart .axis text {
  /* fill: #2b2929; */
  /* font-family: Georgia; */
  font-size: 120%;
}

/* LINE CHART */
.National-LineChart path {
  fill: none;
  stroke: #fff;
}

/* LINE CHART */
.National-LineChart path.line-0 {
  fill: none;
  /* stroke: #de3831; */
}

.National-LineChart .provincial-stats path.line-0 {
  /* stroke: black; */
  /* stroke: #de3831; */
}

.National-LineChart path.line-1 {
  fill: none;
  stroke: #007a4d;
  /* stroke-dasharray: 2; */
}

.National-LineChart path.line-2 {
  fill: none;
  stroke: #ffb612;
  /* stroke-dasharray: 6; */
}

.National-LineChart path.line-3 {
  fill: none;
  stroke: #706f6f;
  stroke-width: 0.7;
  /* stroke-dasharray: 6; */
}

.serie_label {
  fill: #fff;
  /* font-family: Georgia; */
  font-size: 80%;
}

.National-LineChart .provincial-stats {
  position: relative;
  display: flex;
  justify-content: space-between;
  height: 100%;
}

.Dashboard .National-LineChart .provincial-stats header {
  font-size: 0.75rem;
  /* position: absolute; */
  /* top: 1em;
  right: 1em; */
  width: 50%;
  float: right;
  margin: 1em 0 0;
  padding: 0;
}

.National-LineChart .provincial-stats header label {
  display: flex;
}

.National-LineChart .provincial-stats header span {
  display: inline-block;
  margin: auto 0;
}

.National-LineChart .provincial-stats header input {
  height: 16px;
}

.National-LineChart .provincial-stats svg {
  display: none;
}

.National-LineChart .provincial-stats svg:last-of-type {
  display: inline-block;
}

/* .National-LineChart .provincial-stats .linechart {
  width: 100%;
} */

.National-LineChart ul {
  font-size: 0.675rem;
  width: 80px;
  height: 80%;
  display: flex;
  justify-content: space-between;
  flex-direction: column;
  margin: auto 0;
  /* margin-top: 1em; */
  padding: 0 1em;
}

.National-LineChart ul span {
  display: inline-block;
  width: 8px;
  height: 8px;
  border-radius: 50%;
  background-color: #fff;
}

@media (max-width: 50em) {
  .National-LineChart {
    width: 100%;
    margin-bottom: 1em;
  }
}
